/**
  抽奖检查成功后的逻辑状态，根据后端返回的 data.continueDraw, data.share 和 data.maxDrawCounts 合并判断
  ===============================
  1.我要抽奖：活动进行中，有奖品库存，且用户此时有抽奖的资格
  continueDraw：true
  share: true
  maxDrawCounts: false
  2.分享再抽一次：活动进行中，有奖品库存，用户抽奖次数没超过上限值M，但当前没有抽奖资格。
  continueDraw：false
  share:true
  3. 明天再来：活动进行中，有奖品库存，今天的抽奖次数超过上限M值，但是未超过总次数的上限值。
  continueDraw：false
  share:false
  maxDrawCounts:false
  4.已领取，赶快使用吧：活动没结束，但用户抽奖总次数超过上限值M。
  continueDraw：false
  share:false
  maxDrawCounts:true
  =============================
  以上三个值做位运算，如 1，就是 二进制 100，则是4
 **/
export const DRAW_NORMAL = 'DRAW_NORMAL';
export const DRAW_SHARE_ENABLE = 'DRAW_SHARE_ENABLE';
export const DRAW_TOMORROW = 'DRAW_TOMORROW';
export const DRAW_LIMITED = 'DRAW_LIMITED';


